import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Navigate,
} from "react-router-dom";
import Layout from "./hocs/Layout";
import Home from "./containers/Home";
import About from "./containers/About";
import Contact from "./containers/Contact";
import ListingDetail from "./containers/ListingDetail";
import Listings from "./containers/Listings";
import SignIn from "./containers/Signin";
import SignUp from "./containers/Signup";
import NotFound from "./Components/NotFound";

import { Provider } from "react-redux";
import store from "./store";

import "./sass/main.scss";

const App = () => {
  return (
   
    <Provider store={store}>
      <Router>
        <Layout>
          <Routes>
            <Route excat path="/" Component={Home} />
            <Route excat path="/about" Component={About} />
            <Route excat path="/contact" Component={Contact} />
            <Route excat path="/listings" Component={Listings} />
            <Route excat path="/listings/:id" Component={ListingDetail} />
            <Route excat path="/login" Component={SignIn} />
            <Route excat path="/signup" Component={SignUp} />
            <Route path="/404" Component={NotFound} />
            <Route path="*" element={<Navigate to="/404" />} />
          </Routes>
        </Layout>
      </Router>
    </Provider>
  );
};

export default App;
